import React, {Component} from 'react';
import { Map, Marker, Circle } from 'react-amap';
class AMap extends Component {
    constructor(props) {
        super(props);
        this.state = {
            center: {longitude: 120.315394, latitude: 32.851636},
            radius: 20,
            visible: true,
            style: {
                strokeOpacity: 0,
                fillColor: 'rgba(255,0,0,.2)'
            },
            draggable: true,
        }
        this.mapCenter = {longitude: 120.315394, latitude: 32.851636};//地图中心点
        //120.315394,32.851636
        this.markerPosition = {longitude: 120.315394, latitude: 32.851636}//经纬度
    }

    render() {
        return (
            <div>
                <Map
                    amapkey={'788e08def03f95c670944fe2c78fa76f'}
                    zoom={18}
                    center={this.mapCenter}
                >
                    <Circle
                        center={ this.state.center }
                        radius={ this.state.radius }
                        visible={ this.state.visible }
                        style={ this.state.style }
                        draggable={ this.state.draggable }
                    />
                    <Marker position={this.markerPosition} />
                </Map>
            </div>
        );
    }
}

export default AMap;