import React, {Component} from 'react';
import {NavLink} from "react-router-dom";

import './index.css';
import {navlink} from "./navlink";



class NavLeft extends Component {
    render() {
        return (
            <div className='navleft'>
                <ul>
                    {
                        navlink.map((item, index) => {
                            return(
                                    <li key={index}>
                                        <NavLink
                                            exact={item.exact}
                                            to={item.to}
                                        >{item.title}</NavLink>
                                    </li>
                                )


                        })
                    }

                    {/*<li><NavLink exact to="/">首页</NavLink></li>*/}
                    {/*<li><NavLink to="/line">折线图</NavLink></li>*/}
                    {/*<li><NavLink to="/sunburst">旭日图</NavLink></li>*/}
                    {/*<li><NavLink to="/pie">饼图</NavLink></li>*/}
                    {/*/!*<li><NavLink>柱状图</NavLink></li>*!/*/}
                    {/*/!*<li><NavLink>圆形图</NavLink></li>*!/*/}
                    {/*<li><NavLink to="/map">地图</NavLink></li>*/}
                    {/*/!*<li><NavLink>树状图</NavLink></li>*!/*/}
                    {/*/!*<li><NavLink>树形地图</NavLink></li>*!/*/}
                    {/*<li><NavLink to="/gauge">汽车表盘</NavLink></li>*/}
                    {/*/!*<li><NavLink>业务受理进度</NavLink></li>*!/*/}
                </ul>
            </div>
        );
    }
}

export default NavLeft;