import React, {Component} from 'react';
import {option} from "./pie";



class Pie extends Component {
    componentDidMount() {
        //接受父组件传来的数据
        let echarts = this.props.echarts;
        //获取初始化dom
        let element = this.refs.pie;
        //初始化实例
        let myChart = echarts.init(element);
        //渲染图标
        myChart.setOption(option);

    }
    render() {
        return (
            <div ref='pie'></div>
        );
    }
}

export default Pie;