import React, {Component} from 'react';
import {HashRouter as Router, Switch, Route} from 'react-router-dom';

import echarts from "echarts";

import Layout from "./components/layout";
import {routes} from "./router/index.jsx";




class App extends Component {
    render() {
        return (
            <Router basename={'react'}>
                <Switch>
                    <Route path='/' render={()=>{
                        return (
                            <Layout>
                                <Switch>
                                    {
                                        routes.map((item, index) => {
                                         if(item.exact){
                                             return (
                                                 <Route
                                                    key={index}
                                                    exact={item.exact}
                                                    path={item.path}
                                                    component={item.component}
                                                 ></Route>
                                             )
                                         }
                                         return (
                                             <Route
                                                key={index}
                                                path={item.path}
                                                render={()=>{
                                                    return <item.component echarts={echarts} />
                                                }}
                                             ></Route>
                                         )
                                        })
                                    }





                                    {/*<Route exact path='/' component={Home}></Route>*/}

                                    {/*<Route path='/line' render={()=>{*/}
                                        {/*return <Line echarts={echarts}></Line>*/}
                                    {/*}}></Route>*/}
                                    {/*<Route path='/sunburst' render={()=>{*/}
                                        {/*return <Sunburst echarts={echarts}></Sunburst>*/}
                                    {/*}}></Route>*/}
                                    {/*<Route path='/pie' render={()=>{*/}
                                        {/*return <Pie echarts={echarts}></Pie>*/}
                                    {/*}}></Route>*/}
                                    {/*<Route path='/gauge' render={()=>{*/}
                                        {/*return <Gauge echarts={echarts}></Gauge>*/}
                                    {/*}}></Route>*/}
                                    {/*<Route path='/map' render={()=>{*/}
                                        {/*return <AMap></AMap>*/}
                                    {/*}}></Route>*/}
                                    {/*<Route component={NotFound}></Route>*/}
                                </Switch>
                            </Layout>
                        )
                    }}></Route>
                </Switch>
            </Router>
        );
    }
}

export default App;
